@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}

.repo {
  text-align: center;
  width: 75%;
  margin-top: 0px;
  margin: 0px auto auto 100px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 50px;
  box-shadow: 1px 1px 10px grey;
  -webkit-animation: 1s slide-up;
          animation: 1s slide-up;
  margin-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
}

@-webkit-keyframes slide-up {
  from {
    margin-bottom: 200%;
  }

  to {
    margin-top: 0%;
  }
}

@keyframes slide-up {
  from {
    margin-bottom: 200%;
  }

  to {
    margin-top: 0%;
  }
}

h2 {
  color: #17a2b8;
  font-weight: bold;
  margin-top: 0px;
}

.repo__item {
  color: rgb(38, 38, 38);
  text-align: left;
  max-width: 100%;
}

.repo__item:first-of-type {
  margin-top: 45px;
}

.repo__item span {
  font-weight: bold;
}

.form-wrapper {
  text-align: center;
  overflow: hidden;
}

.form-container {
  text-align: left;
  width: 40%;
  margin: auto;
  -webkit-animation: slide-left 1s;
          animation: slide-left 1s;
}

.form-container input:first-of-type {
  margin-bottom: 0.2rem;
  width: 100%;
}

.form-container button:first-of-type {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.form-container p:first-of-type {
  color: red;
}

@-webkit-keyframes slide-left {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 30%;
  }
}

@keyframes slide-left {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 30%;
  }
}

.svg-container {
  text-align: center;
  margin-top: 25px;
  width: 100%;
}

.svg {
  display: block;
  margin: auto;
}

.main-container {
  width: 100%;
}

@media (min-width: 500px) {
  .svg {
    width: 30%;
    height: 100%;
  }
}

.repositories {
  text-align: center;
  width: 100%;
}

.pagination-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: inline-block;
  vertical-align: middle;
  border-radius: 0px;
  border-right: none;
  border-left: none;
  border-bottom: none;
}

.previous-page,
.next-page {
  height: 2.35rem;
  vertical-align: middle;
  border-radius: 0px;
  border-bottom: none;
}

.previous-page {
  border-top-left-radius: 0.25rem;
}

.next-page {
  border-top-right-radius: 0.25rem;
}

.pagination {
  margin-top: 100px;
  width: 75%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  -webkit-animation: 1s slide-left-pagination;
          animation: 1s slide-left-pagination;
}

@-webkit-keyframes slide-left-pagination {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 12.5%;
  }
}

@keyframes slide-left-pagination {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 12.5%;
  }
}

/*com*/

